.header {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  padding: 2px 0;
  background-color: var(--bg);
  color: var(--text);
  box-shadow: 0px 4px 5px rgba(50, 50, 71, 0.16);
  z-index: 3;
  overflow: hidden;

  &__top {
    display: flex;
    align-items: center;
  }

  &__button {
    display: flex;
  }

  &__logo {
    margin: 0 25px;
  }

  &__search {
    margin-left: auto;
  }

  &__contact {
    margin-left: 25px;
  }

  &__callback {
    display: flex;
  }

  &__number {
    display: none;
  }

  &__schedule {
    display: none;
  }

  @include media-min(1024px) {
    position: absolute;
    padding: 0;
    padding-top: 5px;
    box-shadow: none;
    overflow: visible;

    &__top {
      align-items: flex-end;
      margin-bottom: 15px;
    }

    &__logo {
      margin-left: 0;
      margin-right: 58px;
      display: flex;

      & svg {
        width: 140px;
        height: 70px;
      }
    }

    &__button-menu {
      display: none;
    }

    &__search {
      margin-left: 0;
      flex: 1 1 auto;
    }

    &__contact {
      margin-left: 58px;
    }

    &__callback {
      font-weight: 600;
      font-size: 18px;
      line-height: 26px;
      justify-content: flex-end;
    }

    &__contact-icon {
      display: none;
    }

    &__number {
      display: inline-block;
    }

    &__schedule {
      display: inline-block;
      font-size: 14px;
      line-height: 20px;
    }
  }
}

@import "./components/search";
@import "./components/nav";

.hide {
  display: none;
}
