.radiobutton {
  &__item:not(:first-child) {
    margin-left: 12px;
  }

  & label {
    color: var(--text);
    user-select: none;
    cursor: pointer;
    border-radius: 4px;
    padding: 4px 12px;
  }

  & input:checked+label {
    color: var(--text-altern);
    background-color: var(--accent);
    box-shadow:
      0px 4px 4px rgba(50, 50, 71, 0.08),
      0px 4px 8px rgba(50, 50, 71, 0.06);
  }

  & input:focus+label {
    outline: 1px solid var(--accent);
    outline-offset: 2px;
  }
}
