.checkbox {

  & label {
    padding: 4px 0;
    padding-left: 32px;
    color: var(--text);
    user-select: none;
    cursor: pointer;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      margin: 2px 0;
      top: 0;
      left: 0;
      width: 22px;
      height: 22px;
      border: 2px solid var(--checkbox);
      border-radius: 5px;
    }
  }

  & input:checked+label {
    color: var(--accent);

    &::before {
      border-color: var(--accent);
    }

    &::after {
      content: '';
      position: absolute;
      top: 50%;
      left: 5px;
      transform: translateY(-50%);
      width: 12px;
      height: 10px;
      background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' fill='%23359c31' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M4.2426 9.31367L0 5.07097L1.41421 3.65677L4.2426 6.48527L9.8995 0.828369L11.3137 2.24259L4.2426 9.31367Z'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-position: center;
      background-size: 12px 10px;
    }
  }

  & input:focus+label {
    &::before {
      border-color: var(--accent);
    }

    outline: 1px solid var(--accent);
    outline-offset: 2px;
  }

  &__showall {
    color: var(--text-notaccent);
    border-bottom: 1px dashed var(--text-notaccent);
  }
}
