.nav {
  padding: 5px 0 40px;
  color: var(--text-altern);
  background-color: var(--bg-altern);
  font-size: 14px;
  line-height: 20px;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  transform: translateX(-100%);
  height: 100%;
  overflow-y: auto;
  transition: transform .3s ease-in-out;

  &--open {
    transform: translateX(0%);
  }

  &__container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
  }

  &__icon {
    fill: var(--text-altern);
  }

  &__list {
    width: 100%;
    margin: 12px 0;
    padding-bottom: 12px;
    position: relative;


    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: var(--bg);
      opacity: .2;
    }
  }

  &__link {
    font-weight: 600;
    display: flex;
    align-items: center;
    padding: 12px 0;
  }

  &__sublink {
    display: flex;
    align-items: center;
    padding: 12px 0;
  }

  &__sublink-icon {
    fill: var(--text-altern);
    margin-right: 12px;
  }

  &__contact {
    margin-top: 12px;
    display: grid;
  }

  &__callback {
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    color: var(--accent);
    margin-bottom: 4px;
  }

  &__schedule {
    color: #c4c4c4;
  }

  &__email {
    margin: 16px 0;
  }

  &__instagram {
    background-color: var(--accent);
    display: flex;
    padding: 8px;
    width: 36px;
    height: 36px;
    border-radius: 50%;
  }

  @include media-min(1024px) {
    position: static;
    padding: 0;
    transform: translateX(0%);
    height: auto;
    background-color: var(--bg);
    overflow-y: visible;

    &__logo {
      display: none;
    }

    &__button {
      display: none;
    }

    &__list {
      position: relative;
      display: flex;
      justify-content: space-between;
      margin: 0;
      padding: 0;
      background-color: var(--accent);

      &::after {
        display: none;
      }
    }

    &__item {
      flex: 1 1 auto;
      display: flex;
      justify-content: center;
    }

    &__link {
      font-weight: 400;
    }

    &__contact {
      display: none;
    }

    &__submenu {
      background-color: var(--bg-altern);

      &:hover .nav__sublist {
        display: flex;
      }
    }

    &__submenu-link {
      padding-left: 27px;
      background-image: url("data:image/svg+xml,%3Csvg width='18' height='12' viewBox='0 0 18 12' fill='%23ffffff' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0 12H18V10H0V12ZM0 7H18V5H0V7ZM0 0V2H18V0H0Z'/%3E%3C/svg%3E");
      background-repeat: no-repeat;
      background-position: left center;
      background-size: 18px 12px;
    }

    &__sublist {
      display: none;
      justify-content: space-between;
      background-color: var(--bg-altern);
      position: absolute;
      left: 0;
      right: 0;
      top: 100%;
    }

    &__subitem {
      flex: 1 1 auto;
    }

    &__sublink {
      display: grid;
      justify-content: center;
      padding: 25px 20px 20px;

      &:hover {
        background-color: var(--accent);
      }
    }

    &__sublink-icon {
      margin: 0 auto;
      margin-bottom: 10px;
      width: 38px;
      height: 29px;
    }
  }
}
