.product {
  display: grid;
  row-gap: 5px;

  &__title {
    margin: 0;
    font-size: 12px;
    line-height: 18px;
    font-weight: 400;
    color: var(--text);
  }

  &__image {
    order: -1;
  }

  &__price-wrapper {
    display: flex;
    align-items: center;
  }

  &__price {
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;

    &--old {
      color: var(--text-notaccent);
      font-weight: 400;
      font-size: 10px;
      line-height: 12px;
      text-decoration: line-through;
      margin-left: 12px;
    }

    &::after {
      content: ' ₽';
    }
  }

  @include media-min(1024px) {
    row-gap: 10px;
    padding: 16px;
    transition: box-shadow .2s ease-in-out;

    &:hover {
      box-shadow: 0px 20px 20px rgba(50, 50, 71, 0.12), 0px 32px 40px rgba(50, 50, 71, 0.12);
    }

    &__title {
      font-size: 14px;
      line-height: 20px;

    }

    &__price {
      font-size: 18px;
      line-height: 26px;

      &--old {
        font-size: 14px;
        line-height: 20px;
      }
    }
  }
}
