.search {
  display: flex;
  align-items: center;

  &__form {
    position: fixed;
    display: flex;
    top: 60px;
    left: 16px;
    right: 16px;
    box-shadow: 0px 4px 5px rgba(50, 50, 71, 0.16);
    transition: transform .4s ease-in-out;
    transform: translateY(-300px);

    &--showed {
      transform: translateY(0);
    }
  }

  &__input {
    width: 100%;
    padding: 4px 26px 4px 4px;
    border: 1px solid var(--border);
    border-radius: 4px;
    outline-offset: 2px;
  }

  &__submit {
    display: flex;
    position: absolute;
    right: 4px;
    top: 5px;
    outline-offset: 2px;
  }

  &__icon {
    fill: var(--text-notaccent);
  }

  @include media-min(1024px) {
    &__form {
      position: static;
      transform: translateY(0);
      width: 100%;
      box-shadow: none;
    }

    &__input {
      padding: 15px 40px 15px 20px;
    }

    &__submit {
      right: 16px;
      top: 17px;
    }

    &__show-hide {
      display: none;
    }
  }
}
