.filters {
  position: fixed;
  top: 56px;
  right: 0;
  transform: translateX(100%);
  background-color: var(--bg);
  color: var(--text);
  overflow-y: auto;
  height: calc(100% - 56px);
  z-index: 2;
  transition: transform .3s ease-in-out;

  &--open {
    transform: translateX(0%);
  }

  &__title {
    position: relative;
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
    padding: 10px 77px 16px 48px;

    &::after {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: var(--border);
    }
  }

  &__close {
    position: absolute;
    top: 16px;
    right: 20px;
  }

  &__close-icon {
    fill: var(--text-notaccent);
  }

  &__form {
    padding: 16px;
    display: grid;
    row-gap: 24px;
    font-size: 12px;
    line-height: 18px;
  }

  &__field {
    margin: 0;
    padding: 0;
    border: none;
  }

  &__name {
    color: var(--text-gray);
    font-weight: 600;
    font-size: 18px;
    line-height: 26px;
  }

  &__inner {
    width: 100%;
  }

  &__list {
    display: grid;
    row-gap: 4px;
  }

  &__item {
    display: flex;
    align-items: center
  }

  @include media-min(1024px) {
    overflow-y: visible;
    transform: translateX(0%);
    position: static;

    &__title {
      display: none;
    }

    &__close {
      display: none;
    }

    &__form {
      padding: 0;
      font-size: 14px;
      line-height: 20px;
      row-gap: 30px;
    }

    &__name {
      margin-bottom: 15px;
    }
  }
}

@import "./components/radiobutton";
@import "./components/range";
@import "./components/sale";
@import "./components/checkbox";
