.catalog {
  padding: 24px 0 60px;

  &__breadcrumbs {
    margin-bottom: 18px;
  }

  &__title {
    margin: 0;
    margin-bottom: 24px;
    font-size: 24px;
    font-weight: 600;
    line-height: 30px;
  }

  &__tags {
    margin-bottom: 28px;
  }

  &__button {
    position: relative;
    margin-bottom: 26px;
    padding-left: 28px;
    color: var(--text-gray);
    font-weight: 600;
    font-size: 14px;
    line-height: 20px;

    &::before {
      content: '';
      position: absolute;
      left: 0;
      width: 16px;
      height: 16px;
      background-image: url("data:image/svg+xml,%3Csvg width='16' height='16' viewBox='0 0 16 16' fill='%23359C31' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.249934 1.61C2.56993 4.59 5.99993 9 5.99993 9V14C5.99993 15.1 6.89993 16 7.99993 16C9.09993 16 9.99993 15.1 9.99993 14V9C9.99993 9 13.4299 4.59 15.7499 1.61C16.2599 0.95 15.7899 0 14.9499 0H1.03993C0.209934 0 -0.260066 0.95 0.249934 1.61Z'/%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: center;
      background-size: cover;
    }
  }

  &__products {
    position: relative;
    padding: 24px 0 116px;

    &::before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: var(--border);
      opacity: 0.5;
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 16px;

    @include media-min(768px) {
      grid-template-columns: repeat(3, 1fr);
    }
  }

  &__descr-title {
    margin: 0;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
  }

  &__descr-text {
    font-size: 14px;
    line-height: 20px;

    & p:not(:last-of-type) {
      margin-bottom: 15px;
    }
  }

  @include media-min(1024px) {
    padding-top: 195px;

    &__container {
      display: grid;
      grid-template-rows: repeat(1, 1fr);
      grid-template-columns: 240px 1fr;
      column-gap: 75px;
    }

    &__breadcrumbs {
      margin-bottom: 16px;
      grid-column: span 2;
    }

    &__title {
      font-size: 48px;
      line-height: 58px;
      margin-bottom: 40px;
      grid-column: span 2;
    }

    &__tags {
      margin-bottom: 0;
    }

    &__filters {
      grid-row: 3 / 5;
      grid-column: 1 /2;
    }

    &__button {
      display: none;
    }

    &__products {
      padding: 40px 0 170px;
      grid-column: 2 /3;

      &::before {
        display: none;
      }
    }

    &__list {
      row-gap: 32px;
      column-gap: 48px;
    }

    &__descr {
      grid-column: span 2;
    }

    &__descr-title {
      font-size: 36px;
      line-height: 44px;
      margin-bottom: 24px;
    }

    &__descr-text {
      font-size: 18px;
      line-height: 26px;
    }
  }
}

@import "./components/breadcrumbs";
@import "./components/tags";
@import "./components/filters";
@import "./components/product";
