.breadcrubs {
  &__list {
    display: flex;
  }

  &__item {
    font-size: 12px;
    line-height: 18px;
    color: var(--text-notaccent);

    &--current {
      color: var(--text);
      pointer-events: none;
    }

    &:not(:last-child)::after {
      content: '>';
      margin: 0 2px;
      color: var(--text);
    }

    & a {
      cursor: pointer;
    }
  }

  @include media-min(1440px) {
    &__item {
      font-size: 14px;
      line-height: 20px;
    }
  }
}
