.footer {
  padding: 48px 0;
  color: var(--text-altern);
  background-color: var(--bg-altern);
  font-size: 14px;
  line-height: 20px;

  &__logo {
    display: none;
  }

  &__title {
    display: none;
  }

  &__list {
    display: grid;
    row-gap: 16px;
    margin-bottom: 24px;
  }

  &__sublist-wrapper {
    display: none;
  }

  &__sublist {
    padding-top: 12px;
  }

  &__link {
    font-weight: 600;
  }

  &__contact {
    margin-top: 12px;
    display: grid;
  }

  &__callback {
    font-weight: 600;
    font-size: 24px;
    line-height: 30px;
    color: var(--accent);
    margin-bottom: 8px;
  }

  &__schedule {
    color: #c4c4c4;
  }

  &__email {
    margin: 16px 0;
    font-size: 18px;
    line-height: 26px;
  }

  &__instagram {
    background-color: var(--accent);
    display: flex;
    padding: 10px;
    width: 43px;
    height: 43px;
    border-radius: 50%;
    margin-bottom: 24px;
  }

  &__adress {
    margin-bottom: 8px;
  }

  &__politic {
    display: inline-block;
    color: var(--text-notaccent);
    margin-bottom: 24px;
    text-decoration: underline;
  }

  &__copyright {
    color: var(--text-notaccent);
    display: flex;
    flex-direction: column;
  }

  &__sitename {
    margin-bottom: 8px;
  }

  &__devlink {
    text-decoration: underline;
  }

  @include media-min(768px) {
    padding: 45px 0 30px;

    &__container {
      display: grid;
      column-gap: 20px;
      row-gap: 5px;
      grid-template-columns: repeat(3, 1fr);
      grid-template-rows: repeat(1, 1fr);
    }

    &__logo {
      display: block;
      grid-row: 1 / 2;
      grid-column: span 1;
    }

    &__list-wrapper {
      grid-column: 2 / 3;
      grid-row: span 3;
      padding-left: 30px;
    }

    &__list {
      margin-bottom: 0;
      padding-top: 12px;
    }

    &__contact {
      grid-column: 3 / -1;
      grid-row: span 3;
      justify-self: end;
      text-align: right;
    }

    &__instagram {
      justify-self: end;
    }

    &__adress {
      grid-column: 1 / 2;
      margin-bottom: 8px;
    }

    &__politic {
      grid-column: 1 / 2;
    }

    &__copyright {
      grid-column: 1 / -1;
      display: flex;
      flex-direction: row;
      justify-content: space-between;
    }

    &__sitename {
      margin-bottom: 0;
    }
  }

  @include media-min(1024px) {
    &__container {
      grid-template-columns: repeat(4, 1fr);
    }

    &__list-wrapper {
      grid-column: 3/4;
    }

    &__list {
      row-gap: 8px;
    }

    &__link {
      color: var(--text-notaccent);
      font-weight: 400;
      transition: color .2s ease-in-out;

      &:hover {
        color: var(--accent);
      }
    }

    &__sublist-wrapper {
      display: block;
    }

    &__title {
      display: block;
      margin: 15px 0;
      font-size: 18px;
      line-height: 26px;
      font-weight: 600;
    }

    &__sublist {
      display: flex;
      flex-wrap: wrap;
    }

    &__subitem {
      flex-basis: 50%;
    }

    &__sublink {
      color: var(--text-notaccent);
      display: inline-block;
      margin-bottom: 8px;
      transition: color .2s ease-in-out;

      &:hover {
        color: var(--accent);
      }
    }

    &__contact {
      grid-column: 4 / -1;
    }
  }
}
