.range {
  padding-top: 36px;

  &__slider {
    margin: 0 18px;
    margin-bottom: 32px;
  }

  & .noUi-target {
    background-color: var(--border);
    border: none;
  }

  & .noUi-horizontal {
    height: 4px;
  }

  .noUi-horizontal .noUi-handle {
    top: -15px;
  }

  & .noUi-connect {
    background-color: var(--accent);
  }

  & .noUi-handle {
    width: 36px;
    height: 36px;
    border: none;
    border-radius: 50%;
    background-color: var(--bg);
    box-shadow: 0px 2px 2px rgba(50, 50, 71, 0.06), 0px 2px 4px rgba(50, 50, 71, 0.06);
    cursor: pointer;

    &:after,
    &:before {
      display: none;
    }
  }

  &__inputs {
    display: flex;
    justify-content: space-between;
  }

  &__inputs-group {
    position: relative;
  }

  &__label {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    left: 12px;
    color: var(--text-notaccent);
  }

  &__input {
    width: 94px;
    padding: 9px 12px 9px 30px;
    color: var(--text);
    background-color: var(--bg);
    border: 1px solid var(--border);
    border-radius: 4px;
  }

  @include media-min(1024px) {
    padding-top: 6px;

    &__input {
      padding: 9px 12px 9px 34px;
    }
  }
}
