.tags {
  &__list {
    display: flex;
    flex-wrap: wrap;
    margin-bottom: 4px;
  }

  &__item {
    display: flex;
    background-color: var(--accent);
    color: var(--text-altern);
    margin-bottom: 8px;
    padding: 6px 12px;
    box-shadow:
      0px 4px 4px rgba(50, 50, 71, 0.08),
      0px 4px 8px rgba(50, 50, 71, 0.06);
    border-radius: 4px;
    user-select: none;
  }

  &__item:not(:last-of-type) {
    margin-right: 8px;
  }

  &__name {
    font-size: 14px;
    line-height: 20px;
    margin-right: 12px;
  }

  &__cross {
    fill: var(--bg);
  }

  &__clear {
    color: var(--text-notaccent);
    border-bottom: 1px dashed var(--text-notaccent);
  }

  @include media-min(1024px) {
    &__list {
      margin-bottom: 0;
    }
  }

  @include media-min(1440px) {
    display: flex;
    align-items: center;

    &__list {
      margin-bottom: 0;
    }

    &__item {
      margin-bottom: 0;
    }

    &__clear {
      margin-left: 12px;
    }
  }
}
